import React , { useState } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { getCookie } from './action/cookie';

// import pages
import Home from "./pages/Home";
import Login from "./pages/Login";
import Join from "./pages/Join";

import AuthRoute from './action/AuthRoute';
import Deposit from "./pages/Deposit";
import Withdraw from "./pages/Withdraw";
import BetHistory from "./pages/BetHistory";
import PayHistory from "./pages/PayHistory";
import QnaHistory from "./pages/QnaHistory";
import QnaView from "./pages/QnaView";
import ReqQna from "./pages/ReqQna";
import Notice from "./pages/Notice";
import NoticeView from "./pages/NoticeView";
import EnterGameWarning from "./pages/EnterGameWarning";
import DepositSlotMoney from "./pages/DepositSlotMoney";
import ErrorPage from "./pages/ErrorPage"
import Error from "./pages/Error"
// import AlertPopup from "./pages/AlertPopup";


const App = () => {
    const authenticated = getCookie(["authenticated"]) != null;
    console.log(authenticated);
    return (
        // <div className="App">
            <BrowserRouter>

                <Routes>
                    <Route path="/" element={<Home/>}></Route>
                    <Route path="/login" element={<Login authenticated={authenticated}/>}></Route>
                    <Route path="/join" element={<Join authenticated={authenticated}/>}></Route>
                    
                    <Route path="/deposit" element={<Deposit authenticated={authenticated}/>}></Route>
                    <Route path="/withdraw" element={<Withdraw authenticated={authenticated}/>}></Route>
                    <Route path="/betHistory" element={<BetHistory authenticated={authenticated}/>}></Route>
                    <Route path="/payHistory" element={<PayHistory authenticated={authenticated}/>}></Route>
                    <Route path="/qnaHistory" element={<QnaHistory authenticated={authenticated}/>}></Route>
                    <Route path="/qnaView" element={<QnaView authenticated={authenticated}/>}></Route>
                    <Route path="/reqQna" element={<ReqQna authenticated={authenticated}/>}></Route>
                    <Route path="/notice" element={<Notice authenticated={authenticated}/>}></Route>
                    <Route path="/noticeView" element={<NoticeView authenticated={authenticated}/>}></Route>
                    <Route path="/enterGameWarning" element={<EnterGameWarning authenticated={authenticated}/>}></Route>
                    <Route path="/depositSlotMoney" element={<DepositSlotMoney authenticated={authenticated}/>}></Route>
                    <Route path="/error" element={<ErrorPage/>}></Route>
                    <Route path="*" element={ <Error /> }/>
                </Routes>
            </BrowserRouter>
        // </div>

    );
}

export default App;