import React, { useEffect, useState } from "react";
import { Link, Navigate } from 'react-router-dom';
import { getCookie } from '../action/cookie';
import axios from 'axios';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import ok_icon from "../assets/images/sub/ok_icon.png";

const Deposit = props => {

    const [userId, setUserId] = useState("");
    const [depositMoney, setDepositMoney] = useState("");
    const [nick, setNick] = useState("");
    const [bankHolder, setBankHolder] = useState("");
    const [phoneNumber, setPhoneNumber] = useState("");
    const [coupon, setCoupon] = useState("");

    const [modalShow, setModalShow] = useState(false);
    const [modalDisplay, setModalDisplay] = useState('none');
    const [modalTitle, setModalTitle] = useState("");
    const [modalContent, setModalContent] = useState("");


    useEffect(() => {
        console.log(window.sessionStorage);
        setUserId(getCookie(["userid"]));
        setNick(getCookie(["nick"]));
        setBankHolder(getCookie(["bankHolder"]));
        setPhoneNumber(getCookie(["phoneNumber"]));
    }, []);
    console.log(props.authenticated)
    if (!props.authenticated) return <Navigate to={"/"} />;

    function DepositPopup(props) {
        return (
            <div id="popup2" style={{display: props.display}}>
              <div className="box">
                <h1> {props.title} </h1>
                <p  className="txt"> {props.content} </p>
                <div className="ok"><a href="#" onClick={props.onHide}>확인</a></div>
              </div>
            </div>
        );
    }

    function showPopup(title, content) {
        setModalTitle(title);
        setModalContent(content);
        setModalShow(true);
        setModalDisplay('block');
    }

    function closePopup() {
        setModalShow(false);
        setModalDisplay('none');
    }


    async function reqDeposit () {
        if(depositMoney == 0) {
            showPopup('입금요청 실패', '입금금액을 정확히 선택해주세요');
        } else {
            console.log(depositMoney);
            console.log(nick);
            console.log(bankHolder);
            console.log(phoneNumber);
            axios("/users/reqDeposit", {
                method:"POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                data: {
                    userid: userId,
                    depositMoney: depositMoney,
                }
            })
            .then(function(res){
                console.log(res.data)
                if(res.data.statusCode == 200){
                    showPopup('입금요청 성공', '입금요청이 성공하였습니다.');
                } else {
                    showPopup('입금요청 실패 ', '입금요청이 실패하였습니다. 잠시후 다시 시도해 주세요.');
                }
            });
        }
    };

    async function reqBankAccount() {
        if(userId == null) {
            showPopup('입금계좌문의 실패', '로그인후에 요청이 가능합니다.');
        } else {
            axios("/users/reqBankAccount", {
                method:"POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                data: {userid: userId}
            })
            .then(function(res){
                if(res.data.statusCode == 200){
                    showPopup('입금계좌문의 성공', '입금계좌 요청이 성공하였습니다.');
                } else {
                    showPopup('입금계좌문의 실패 ', '입금계좌 요청이 실패하였습니다. 잠시후 다시 시도해 주세요.');
                }
            });
        }
    }

    
    return (
        <>
        <DepositPopup
            show={modalShow}
            display={modalDisplay}
            title={modalTitle}
            content={modalContent}
            onHide={() => closePopup()}
        />
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt="월드슬롯"/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li><Link to="/deposit" className="on">입금신청</Link></li>
                    <li><Link to="/withdraw">출금신청</Link></li>
                    <li><Link to="/betHistory">베팅내역</Link></li>
                    <li><Link to="/payHistory">입출내역</Link></li>
                    <li><Link to="/qnaHistory">문의내역</Link></li>
                    <li><Link to="/notice">공지사항</Link></li>
                </ul>
                <div className="content">
                    <div class="inquiry"><a href="#" onClick={reqBankAccount}>입금계좌문의</a>※ 입금시 꼭 계좌문의를 하세요!</div>
                    <table className="board-write">
                        <tbody>
                            <tr>
                                <th>입금금액</th>
                                <td>
                                    <input type="text" value={depositMoney.toLocaleString('ko-KR')} onChange={(e) => setDepositMoney(e.target.value)} placeholder="최소입금 10,000부터" />
                                    <ul className="amount">
                                        <li><a href="#" onClick={(e) => setDepositMoney(10000)}>만원</a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(50000)}>오만원</a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(100000)}>십만원</a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(500000)}>오십만원</a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(1000000)}>백만원</a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney("")} className="reset" >초기화</a></li>
                                    </ul>
                                </td>
                            </tr>
                            <tr>
                                <th>회원정보</th>
                                <td><input type="text" value={nick} disabled={true} placeholder="홍길동" /></td>
                            </tr>
                            <tr>
                                <th>입금자명</th>
                                <td>
                                    <input type="text" value={bankHolder} disabled={true} placeholder="홍길동" />
                                    <p className="care">※ 입금자명이 다른 경우 이름을 수정 후 신청 하시기 바랍니다.</p>
                                </td>
                            </tr>
                            <tr>
                                <th>휴대폰번호</th>
                                <td><input type="text" value={phoneNumber} disabled={true} placeholder="01000000000" /></td>
                            </tr>
                            <tr>
                                <th>쿠폰선택</th>
                                <td>
                                    <select value={coupon} onChange={(e) => setCoupon(e.target.value)}>
                                        <option>쿠폰사용</option>
                                    </select>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="button"><a href="#" className="on" onClick={reqDeposit}><img src={ok_icon} alt="신청"/>입금신청</a></div>
                </div>
            </div>
        </div>
        </>
    );
}

export default Deposit;
