import React, { useState, useEffect } from "react";

import { Link, Navigate } from 'react-router-dom';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import ok_icon from "../assets/images/sub/ok_icon.png";
import list_icon from "../assets/images/sub/list_icon.png";


const ReqQna = props => {
    const [userid, setUserId] = useState("");
    const [nick, setNick] = useState("");
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");

    useEffect(() => {
        console.log(window.sessionStorage);
        setUserId(window.sessionStorage.getItem("userid"));
        setNick(window.sessionStorage.getItem("nick"));
    }, []);
    console.log(props.authenticated);

    if (!props.authenticated) return <Navigate to={"/"} />;

    function reqQuestion () {
        
        console.log(userid);
        console.log(nick);
        fetch("/users/registerQna", {
            method:"POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                userid: userid,
                nick: nick,
                title: title,
                content: content,
            })
        })
        .then(data=>data.json());

    };

    return (
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt="월드슬롯"/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li><Link to="/deposit">입금신청</Link></li>
                    <li><Link to="/withdraw">출금신청</Link></li>
                    <li><Link to="/betHistory">베팅내역</Link></li>
                    <li><Link to="/payHistory">입출내역</Link></li>
                    <li><Link to="/qnaHistory" className="on">문의내역</Link></li>
                    <li><Link to="/notice">공지사항</Link></li>
                </ul>
                <div className="content">
                    <table className="board-write">
                        <tbody>
                            <tr>
                                <th>제목</th>
                                <td><input type="text" value={title} onChange={(e) => setTitle(e.target.value)} placeholder="제목을 입력해주세요"/></td>
                            </tr>
                            <tr>
                                <th>내용</th>
                                <td><textarea value={content} onChange={(e) => setContent(e.target.value)} placeholder="내용을 입력해주세요"></textarea></td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="button">
                        <Link to="/qnaHistory" className="on" onClick={reqQuestion} ><img src={ok_icon} alt="확인"/>확인</Link>
                        <Link to="/qnaHistory"><img src={list_icon} alt="목록"/>목록</Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ReqQna;
