import React, { useState } from "react";
import { Link, Navigate } from 'react-router-dom';
import axios from 'axios';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import add_icon from "../assets/images/sub/add_icon.png";

import { bankList } from "../const";


const Join = props => {

    const [userId, setUserId] = useState("");
    const [password, setPassword] = useState("");
    const [repassword, setRePassword] = useState("");
    const [nick, setNick] = useState("");
    const [year, setYear] = useState("");
    const [month, setMonth] = useState("");
    const [day, setDay] = useState("");
    const [tel1, setTel1] = useState("");
    const [tel2, setTel2] = useState("");
    const [tel3, setTel3] = useState("");
    const [bank, setBank] = useState("");
    const [bankHolder, setBankHolder] = useState("");
    const [bankAccount, setBankAccount] = useState("");
    const [recCode, setRecCode] = useState("");

    const [modalShow, setModalShow] = useState(false);
    const [modalDisplay, setModalDisplay] = useState('none');
    const [modalTitle, setModalTitle] = useState("");
    const [modalContent, setModalContent] = useState("");
    console.log(props.authenticated);

    if (props.authenticated) return <Navigate to={"/"} />;

    async function RegisterUser(userInfo) {
        return axios("/reqRegister", {
            method:"POST",
            headers: {
                'Content-Type': 'application/json'
            },
            data: userInfo
        })
        .then(function(res){
            if(res.data.statusCode == 200){
                showPopup('등록 성공', '요청이 승인되는 동안 기다려주세요.');
            } else if(res.data.statusCode == 204) {
                showPopup('등록 실패 ', '이미 이용중인 아이디입니다.');
            }
        });
    }

    async function checkId(userid) {
        return axios("/checkUserId", {
            method:"POST",
            headers: {
                'Content-Type': 'application/json'
            },
            data: userid
        })
        .then(res=>res.data)
    }

    function JoinPopup(props) {
        return (
            <div id="popup2" style={{display: props.display}}>
              <div className="box">
                <h1> {props.title} </h1>
                <p  className="txt"> {props.content} </p>
                <div className="ok"><a href="#" onClick={props.onHide}>확인</a></div>
              </div>
            </div>
        );
    }

    function showPopup(title, content) {
        setModalTitle(title);
        setModalContent(content);
        setModalShow(true);
        setModalDisplay('block');
    }

    function closePopup() {
        setModalShow(false);
        setModalDisplay('none');
    }

    const reqRegister = async e => {
        e.preventDefault();

        if(password != repassword) {
            showPopup('등록 실패', '비밀번호가 일치하지 않습니다.');
        }
        if(userId == "" || password == "" || nick == "" || year == "" ||
         month == "" || day == "" || tel1 == "" || tel2 == "" || tel3 == "" ||
         bank == "" || bankHolder == "" || bankAccount == "" || recCode == ""
        ) {
            showPopup('등록 실패', '모든 항목을 다 기재 하셔야 합니다.');
        }
        const token = await RegisterUser({
            userId,
            password,
            nick,
            year,
            month,
            day,
            tel1,
            tel2,
            tel3,
            bank,
            bankHolder,
            bankAccount,
            recCode,

        });
    }

    const checkUserId = async e => {
        e.preventDefault();
        if(userId == "") {
            showPopup('증복확인 실패', '아이디를 정확히 입력해주세요');
        } else {
            const response = await checkId({userId});
            if(response.statusCode == 200) {
                showPopup('증복확인 성공', '이용가능한 아이디입니다');
            } else {
                showPopup('증복확인 성공', '이용중인 아이디입니다');
            }
        }
    }

    return (
        <>
        <JoinPopup
            show={modalShow}
            display={modalDisplay}
            title={modalTitle}
            content={modalContent}
            onHide={() => closePopup()}
        />
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt="월드슬롯"/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li className="member"><Link to="/login">로그인</Link></li>
                    <li className="member"><Link to="/join" className="on">회원가입</Link></li>
                </ul>
                <div className="content">
                    <table className="board-write2">
                        <tbody>
                            <tr>
                                <th>닉네임</th>
                                <td><input type="text" value={nick} onChange={(e) => setNick(e.target.value)} placeholder="닉네임" /></td>
                                <th>아이디</th>
                                <td>
                                    <div className="check">
                                        <input type="text" value={userId} onChange={(e) => setUserId(e.target.value)} placeholder="아이디" />
                                        <a href="#" onClick={checkUserId}>중복확인</a>
                                    </div>
                                    <p className="txt">※ 영문, 숫자만 입력이 가능하며 최소 3자 이상 입력하세요.</p>
                                </td>
                            </tr>
                            <tr>
                                <th>비밀번호</th>
                                <td>
                                    <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="비밀번호" />
                                    <p className="txt">※ 비밀번호는 최소 8자리 이상 영문+숫자+특수문자 조합으로 입력하세요.</p>
                                </td>
                                <th>비밀번호 확인</th>
                                <td>
                                    <input type="password" value={repassword} onChange={(e) => setRePassword(e.target.value)} placeholder="비밀번호 확인" />
                                    <p className="txt">※ 비밀번호를 다시 입력하세요.</p>
                                </td>
                            </tr>
                            <tr>
                                <th>생년월일</th>
                                <td>
                                    <input type="number" value={year} onChange={(e) => setYear(e.target.value)} className="tel" placeholder="년" min="1900" max="2100"/>
                                    <input type="number" value={month} onChange={(e) => setMonth(e.target.value)} className="tel" placeholder="월" min="1" max="12" />
                                    <input type="number" value={day} onChange={(e) => setDay(e.target.value)} className="tel" placeholder="일" min="1" max="31"/>
                                </td>
                                <th>휴대전화</th>
                                <td>
                                    <input type="tel" value={tel1} onChange={(e) => setTel1(e.target.value)} className="tel" />
                                    <input type="tel" value={tel2} onChange={(e) => setTel2(e.target.value)} className="tel" />
                                    <input type="tel" value={tel3} onChange={(e) => setTel3(e.target.value)} className="tel" />
                                </td>
                            </tr>
                            <tr>
                                <th>은행</th>
                                <td>
                                    <select value={bank} onChange={(e) => setBank(e.target.value)}>
                                        <option>--- 선택 ---</option>
                                        {bankList.map(({ value, label }, index) => <option value={value} key={value} >{label}</option>)}
                                    </select>
                                </td>
                                <th>예금주</th>
                                <td><input type="text" value={bankHolder} onChange={(e) => setBankHolder(e.target.value)} placeholder="예금주" /></td>
                            </tr>
                            <tr>
                                <th>계좌번호</th>
                                <td><input type="text" value={bankAccount} onChange={(e) => setBankAccount(e.target.value)} placeholder="계좌번호" /></td>
                                <th>추천코드</th>
                                <td>
                                    <input type="text" value={recCode} onChange={(e) => setRecCode(e.target.value)} placeholder="추천코드" />
                                    <p className="txt">※ 추천 코드가 반드시 필요합니다.</p>
                                </td>
                            </tr>
                        </tbody>
                        
                    </table>
                    <div className="button"><a href="#" className="on" onClick={reqRegister}><img src={add_icon} alt="회원가입"/>회원가입</a></div>
                </div>
            </div>
        </div>
        </>
    );
}

export default Join;
