import React, { useState } from "react";
import { Link, Navigate, useLocation } from 'react-router-dom';
import axios from 'axios';
import { getCookie, removeCookie, setCookie } from '../action/cookie';


import logo from "../assets/images/common/logo.png";
import ok_icon from "../assets/images/sub/ok_icon.png";
import close_btn from "../assets/images/sub/close_btn.png";
import warning_img from "../assets/images/main/warning_img.png";
import cancel_icon from "../assets/images/sub/cancel_icon.png";


const DepositSlotMoney = props => {
    console.log(props.authenticated);
    const [userId, setUserId] = useState(getCookie(["userid"]));
    const [nick, setNick] = useState(getCookie(["nick"]));
    const [money, setMoney] = useState(getCookie(["money"]));
    const [depositMoney, setDepositMoney] = useState("");

    const [modalShow, setModalShow] = useState(false);
    const [modalDisplay, setModalDisplay] = useState('none');
    const [modalTitle, setModalTitle] = useState("");
    const [modalContent, setModalContent] = useState("");

    if (!props.authenticated) return <Navigate to={"/"} />;

    async function reqSlotDeposit () {
        console.log('depositMoney: ', depositMoney);
        if(depositMoney == 0) {
            showPopup('입금요청 실패', '입금금액을 정확히 선택해주세요');
        } else {
            console.log(userId);
            console.log(depositMoney);
            axios("/users/reqSlotMoney", {
                method:"POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                data: {
                    userid: userId,
                    type: "deposit",
                    depositMoney: depositMoney,
                }
            })
            .then(function(res){
                console.log(res.data)
                if(res.data.statusCode == 200){
                    setMoney(money - depositMoney);
                    setCookie('money', money - depositMoney);
                    showPopup('충전요청 성공', '충전요청이 성공하였습니다.');
                } else {
                    showPopup('충전요청 실패 ', '충전요청이 실패하였습니다. 잠시후 다시 시도해 주세요.');
                }
            });
        }
    };

    function DepositPopup(props) {
        return (
            <div id="popup2" style={{display: props.display}}>
              <div className="box">
                <h1> {props.title} </h1>
                <p  className="txt"> {props.content} </p>
                <div className="ok"><a href="#" onClick={props.onHide}>확인</a></div>
              </div>
            </div>
        );
    }
    
    function showPopup(title, content) {
        setModalTitle(title);
        setModalContent(content);
        setModalShow(true);
        setModalDisplay('block');
    }

    function closePopup() {
        setModalShow(false);
        setModalDisplay('none');
    }

    return (
        <>
        <DepositPopup
            show={modalShow}
            display={modalDisplay}
            title={modalTitle}
            content={modalContent}
            onHide={() => closePopup()}
        />
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><a href="/"><img src={logo} alt="월드슬롯"/></a></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <div className="content">
                    <table className="board-write">
                        <tr>
                            <th>보유금액</th>
                            <td>
                                <input type="text" value={money.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} disabled={true} placeholder="450" />
                            </td>
                        </tr>
                        <tr>
                            <th>입금금액</th>
                            <td>
                                <input type="text" disabled={true} value={depositMoney.toLocaleString('ko-KR')} onChange={(e) => setDepositMoney(e.target.value)} placeholder="최소입금 10,000부터" />
                                <ul className="amount">
                                    <li><a href="#" onClick={(e) => setDepositMoney(10000)}>만원</a></li>
                                    <li><a href="#" onClick={(e) => setDepositMoney(50000)}>오만원</a></li>
                                    <li><a href="#" onClick={(e) => setDepositMoney(100000)}>십만원</a></li>
                                    <li><a href="#" onClick={(e) => setDepositMoney(500000)}>오십만원</a></li>
                                    <li><a href="#" onClick={(e) => setDepositMoney(1000000)}>백만원</a></li>
                                    <li><a href="#" onClick={(e) => setDepositMoney("")} className="reset" >초기화</a></li>
                                </ul>
                            </td>
                        </tr>
                    </table>
			        <div className="button"><a href="#" className="on" onClick={reqSlotDeposit} ><img src={ok_icon} alt="신청"/>입금신청</a></div>
                </div>
            </div>
        </div>
        </>
    );
}

export default DepositSlotMoney;