import React, { useEffect, useState } from "react";
import { Link, Navigate } from 'react-router-dom';
import DataTable from "react-data-table-component";
import Pagination from "react-js-pagination";

import axios from 'axios';
import { setCookie, getCookie, removeCookie } from '../action/cookie';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import register_icon from "../assets/images/sub/register_icon.png";
import delete_icon from "../assets/images/sub/delete_icon.png";


const QnaHistory = props => {
    console.log(props.authenticated);
    const [userId, setUserId] = useState(getCookie(["userid"]));
    const [qnaHistory, SetQnaHistory] = useState([]);
    const [currentHistory, SetCurrentHistory] = useState([]);

    const [page, setPage] = useState(1);
    const pageLimit = 10;

    useEffect(() => {
        if (props.authenticated) {
            (async () => {
                console.log(userId[0]);
                const response = axios("/users/reqQnaHistory", {
                    method:"POST",
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    data: {
                        userId: userId[0]
                    }
                })
                .then(res=> 
                    {
                        SetQnaHistory(res.data.data);
                        console.log(res.data.data);
                        console.log(qnaHistory);
                        const aryCurrent = [];
                        const aryHistory = res.data.data.slice(0, pageLimit);
    
                        for (var i = 0; i < aryHistory.length; i++) {
                            var data = aryHistory[i];
                            data.id = i + 1;
                            console.log(data)
                            aryCurrent.push(data)
                        }
                        SetCurrentHistory(aryCurrent);
                        console.log(currentHistory);
                    });
            })();
        }
    }, []);

    if (!props.authenticated) return <Navigate to={"/"} />;

    const handlePageChange = (page) => {
        setPage(page);
        console.log(page);

        const offset = (page - 1) * pageLimit;
        const aryHistory = qnaHistory.slice(offset, offset + pageLimit);
        const aryCurrent = [];

        for (var i = 0; i < aryHistory.length; i++) {
            var data = aryHistory[i];
            data.id = offset + i + 1;
            console.log(data)
            aryCurrent.push(data)
        }

        SetCurrentHistory(aryCurrent);
    };

    
    return (
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt="월드슬롯"/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li><Link to="/deposit">입금신청</Link></li>
                    <li><Link to="/withdraw">출금신청</Link></li>
                    <li><Link to="/betHistory">베팅내역</Link></li>
                    <li><Link to="/payHistory">입출내역</Link></li>
                    <li><Link to="/qnaHistory" className="on">문의내역</Link></li>
                    <li><Link to="/notice">공지사항</Link></li>
                </ul>
                <div className="content">
                    <table className="board-list">
                        <colgroup>
                            <col style={{width:"5%"}} />
                            <col style={{width:"2%"}} />
                            <col />
                            <col style={{width:"15%"}} />
                            <col style={{width:"15%"}} />
                        </colgroup>
                        <thead>
                            <tr>
                                <th></th>
                                <th>번호</th>
                                <th>제목</th>
                                <th>일자</th>
                                <th>답변여부</th>
                            </tr>
                        </thead>
                        <tbody>
                            {currentHistory.map(({_id, id, title, status, registerdate }) => (
                                <tr key={_id}>
                                    <td><input type="checkbox" /></td>
                                    <td>{id}</td>
                                    <td>{title}</td>
                                    <td>{registerdate}</td>
                                    <td>{status}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                    <Pagination
                        activePage={page}
                        itemsCountPerPage={pageLimit}
                        totalItemsCount={qnaHistory.length}
                        pageRangeDisplayed={5}
                        firstPageText={<img src="../images/sub/page_first.png" alt="첫 페이지"/>}
                        prevPageText={<img src="../images/sub/page_prev.png" alt="이전 페이지"/>}
                        nextPageText={<img src="../images/sub/page_next.png" alt="다음 페이지"/>}
                        lastPageText={<img src="../images/sub/page_last.png" alt="마지막 페이지"/>}

                        onChange={handlePageChange}
                    />
                    <div className="button">
                        <Link to="/reqQna" className="on"><img src={register_icon} alt="등록"/>등록</Link>
                        <a href="#"><img src={delete_icon} alt="삭제"/>삭제</a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default QnaHistory;