import React, { useState, useEffect } from "react";
import { Link, Navigate } from 'react-router-dom';
import { getCookie } from '../action/cookie';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import ok_icon from "../assets/images/sub/ok_icon.png";


const Withdraw = props => {
    const [userid, setUserId] = useState("");
    const [nick, setNick] = useState("");
    const [userMoney, setUserMoney] = useState("");
    const [withdrawMoney, setWithdrawMoney] = useState("");
    const [bank, setBank] = useState("");
    const [bankHolder, setBankHolder] = useState("");
    const [bankAccount, setBankAccount] = useState("");

    useEffect(() => {
        console.log(window.sessionStorage);
        setUserId(getCookie(["userid"]));
        setNick(getCookie(["nick"]));
        setUserMoney(getCookie(["money"]));
        setBank(getCookie(["bank"]));
        setBankHolder(getCookie(["bankHolder"]));
        setBankAccount(getCookie(["bankAccount"]));
    }, []);
    console.log(props.authenticated);

    if (!props.authenticated) return <Navigate to={"/"} />;

    function reqWithdraw () {
        
        console.log(nick);
        console.log(withdrawMoney);
        console.log(bankHolder);
        console.log(bankAccount);
        fetch("/users/reqWithdraw", {
            method:"POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                userid: userid,
                nick: nick,
                bank: bank,
                bankHolder: bankHolder,
                bankAccount: bankAccount,
                withdrawMoney: withdrawMoney,
            })
        })
        .then(data=>data.json());

    };


    return (
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt="월드슬롯"/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li><Link to="/deposit">입금신청</Link></li>
                    <li><Link to="/withdraw"  className="on">출금신청</Link></li>
                    <li><Link to="/betHistory">베팅내역</Link></li>
                    <li><Link to="/payHistory">입출내역</Link></li>
                    <li><Link to="/qnaHistory">문의내역</Link></li>
                    <li><Link to="/notice">공지사항</Link></li>
                </ul>
                <div className="content">
                    <table className="board-write">
                        <tbody>
                            <tr>
                                <th>보유금액</th>
                                <td><input type="text" value={userMoney} disabled={true} placeholder="1,100" /></td>
                            </tr>
                            <tr>
                                <th>출금금액</th>
                                <td>
                                    <input type="text" value={withdrawMoney.toLocaleString('ko-KR')} onChange={(e) => setWithdrawMoney(e.target.value)} placeholder="최소출금 10,000부터" />
                                    <ul className="amount">
                                        <li><a href="#" onClick={(e) => setWithdrawMoney(10000)}>만원</a></li>
                                        <li><a href="#" onClick={(e) => setWithdrawMoney(50000)}>오만원</a></li>
                                        <li><a href="#" onClick={(e) => setWithdrawMoney(100000)}>십만원</a></li>
                                        <li><a href="#" onClick={(e) => setWithdrawMoney(500000)}>오십만원</a></li>
                                        <li><a href="#" onClick={(e) => setWithdrawMoney(1000000)}>백만원</a></li>
                                        <li><a href="#" onClick={(e) => setWithdrawMoney("")} className="reset" >초기화</a></li>
                                    </ul>
                                </td>
                            </tr>
                            <tr>
                                <th>은행</th>
                                <td><input type="text" value={bank} disabled={true} placeholder="국민은행" /></td>
                            </tr>
                            <tr>
                                <th>예금주</th>
                                <td><input type="text" value={bankHolder} disabled={true} placeholder="홍길동" /></td>
                            </tr>
                            <tr>
                                <th>계좌번호</th>
                                <td>
                                    <input type="text" value={bankAccount} disabled={true} placeholder="09050000000000" />
                                    <p className="care">※ 입금, 출금은 가입시 기재하신 계좌로만 출금 가능 합니다.</p>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="button"><a href="#" className="on" onClick={reqWithdraw}><img src={ok_icon} alt="신청"/>출금신청</a></div>
                </div>
            </div>
        </div>
    );
}

export default Withdraw;
